.product-page .product-section-white {
  height: 648px;
  background-color: var(--color-white);

  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.product-page .product-section-white .product-section-desc {
  width: 400px;
}
.product-page .product-section-white .product-section-desc h4 {
  font-family: Play;
  font-style: normal;
  font-weight: normal;
  font-size: 56px;
  line-height: 65px;
  text-align: center;

  color: #000000;
  margin-bottom: 65px;
}

.product-page .product-section-white .product-section-desc p {
  font-family: Play;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 37px;
}

.product-page .product-section-black {
  height: 648px;
  background-color: var(--color-black-2);
  background-repeat: no-repeat;

  background-position: bottom left;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;

  padding-right: 95px;
}
.product-page .product-section-black .product-section-desc {
  width: 400px;
  color: var(--color-white);
}
.product-page .product-section-black .product-section-desc h4 {
  font-family: Play;
  font-style: normal;
  font-weight: normal;
  font-size: 56px;
  line-height: 65px;
  text-align: center;

  color: var(--color-white);
  margin-bottom: 65px;
}

.product-page .product-section-black .product-section-desc p {
  font-family: Play;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 37px;
}

.product-page .product-section-video {
  height: 975px;
}
.product-page .product-section-video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.bike-spec {
  background-color: var(--color-black);
  padding: 80px 100px;
  background-image: url("../../assets/images/product/spec-bg.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.bike-spec .header {
  font-family: Play;
  font-size: 60px;
  font-style: normal;
  font-weight: 700;
  line-height: 69px;
  letter-spacing: 0em;
  text-align: left;

  color: #ffffff;

  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 90px;
}

.bike-spec .header span {
  font-family: Play;
  font-size: 60px;
  font-style: normal;
  font-weight: 400;
  line-height: 69px;
  letter-spacing: 0em;
  text-align: left;
  padding-left: 10px;
}

.bike-spec .section-header {
  font-family: Play;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 42px;

  text-align: left;
  color: #ffffff;
  border-bottom: 1px solid #ffffff;

  padding-bottom: 18px;
}

.bike-spec .spec-section {
  color: #ffffff;
}

.bike-spec .spec-section .spec-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-left: 30%;
  padding-top: 100px;
}
.bike-spec .spec-section .spec-grid .spec-grid-item {
  padding: 20px;
  box-sizing: border-box;
  width: 50%;
  text-align: left;
}
.bike-spec .spec-section .spec-grid .spec-grid-item h4 {
  font-family: Play;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;

  color: #ffffff;
  max-width: 380px;
  margin-bottom: 17px;
}

.bike-spec .spec-section .spec-grid .spec-grid-item h5 {
  font-family: Play;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;

  color: #e5e5e5;

  margin-bottom: 5px;
}
.bike-spec .spec-section .spec-grid .spec-grid-item p {
  font-family: Arial;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;

  max-width: 380px;
  color: #e5e5e5;

  margin-bottom: 21px;
}

.bike-spec .spec-section .spec-grid .spec-grid-item .sm {
  font-family: Arial;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  align-items: center;

  color: #e5e5e5;
}

.product-page .featured-products {
  background: var(--color-white);
  padding: 70px 0;
}
.product-page .featured-products h4 {
  font-family: Play;
  font-style: normal;
  font-weight: bold;
  font-size: 34px;
  line-height: 39px;
  text-align: center;

  color: var(--color-black);
}
.product-page .featured-products .items {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 180px;
  margin-top: 55px;
}
.product-page .featured-products .items .item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.product-page .featured-products .items .item h4 {
  font-family: Play;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;

  color: #000000;
}
.product-page .featured-products .items .item h5 {
  font-family: Play;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  /* identical to box height */

  color: #8a1900;
}
.product-page .featured-products .items .item .image {
  display: flex;
  height: 280px;
  max-width: 245px;
  padding: 30px;
}
.product-page .featured-products .items .item .image img {
  width: 100%;
}
.product-page .featured-products .items .item .add-to-cart {
  font-family: Play;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  /* identical to box height */

  color: #ffffff;

  background: #000000;
  border-radius: 30px;
  padding: 10px 20px;
}

.product-page .featured-products .items .item .plus {
  position: absolute;
  right: 0;
  bottom: 50px;
  width: 30px;
  height: 30px;
  border: 0;
  outline: 0;
  background: transparent;
  background-image: url("../../assets/icons/plus-small.svg");
  background-repeat: no-repeat;
  background-size: cover;
}
